import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Menu } from "lucide-react";

export function DashboardHeader({ userData, onProfileClick, onMenuClick }) {
  const student = {
    name: `${userData.firstName} ${userData.lastName}`,
    id: userData.studentId,
    avatar: userData.avatar,
  };

  return (
    <header className="glass-header fixed top-0 left-0 right-0 z-50 px-2 py-1.5 sm:px-4 sm:py-2">
      <div className="flex items-center justify-between px-2 sm:px-4">
        <div className="flex items-center gap-2 sm:gap-3">
          <button 
            onClick={onMenuClick}
            className="p-2 rounded-md hover:bg-white/20 transition-colors sm:hidden"
            aria-label="Open menu"
          >
            <Menu className="h-6 w-6 text-foreground" />
          </button>
          <div className="flex items-center gap-1 sm:gap-2">
            <img 
              src="/app-logo.png" 
              alt="App Logo" 
              className="h-10 w-10 sm:h-12 sm:w-12 drop-shadow-md"
            />
            <span className="text-xl sm:text-2xl font-bold text-foreground">
              TrackMate
            </span>
          </div>
        </div>

        <div 
          className="flex items-center gap-2 sm:gap-3 cursor-pointer rounded-lg p-0.5 sm:p-1"
          onClick={onProfileClick}
        >
          <div className="text-right hidden sm:block">
            <p className="text-base sm:text-lg font-medium text-foreground">
              {student.name}
            </p>
            <p className="text-sm sm:text-base text-muted-foreground">{student.id}</p>
          </div>
          <Avatar className="h-8 w-8 sm:h-10 sm:w-10 ring-2 ring-primary/30">
            <AvatarImage
              src={student.avatar || "/student-profile.png"}
              alt={student.name}
            />
            <AvatarFallback className="bg-primary text-primary-foreground text-sm sm:text-base">
              {student.name
                .split(" ")
                .map((n) => n[0])
                .join("")}
            </AvatarFallback>
          </Avatar>
        </div>
      </div>
    </header>
  );
}